<template>
  <div
    class="transaction"
    @click="
      $router.push(
        `/${isSale ? 'transaction_sale' : 'transaction_buy'}/${dt.orderId}?m=${dt.sellerType === 'Business'  ? 1 : 0}`
      )
    "
  >
    <span :class="isSale ? 'out' : 'in'" class="inout bg">
      {{ isSale ? "卖" : "买" }}
    </span>
    <span class="time">
      {{ ymd }}
      <br/>
      {{ hms }}
    </span>
    <span class="amountEl">
      <span :class="isSale ? 'out' : 'in'" class="amount">
        {{ dt.amount | amountFormat() }}
      </span>
      <span class="unit"> V币 </span>
    </span>
    <span :class="dt.status | transactingOrderStatus" class="order-status">
      {{ dt.status | transactingOrderStatus("seller-content") }}
    </span>
  </div>
</template>

<script>
export default {
  name: "TransactionItem",
  props: {
    dt: {
      type: Object,
      default: () => ({
        orderType: 1,
        time: new Date().toLocaleDateString(),
        amount: "0",
        orderStatus: "WaitingSellerConfirm",
      }),
    },
  },
  computed: {
    orderTime() {
      return this.$options.filters.dateFormat(
        this.dt.createTime,
        "Y/M/D h:m:s"
      );
    },
    ymd() {
      return this.orderTime.slice(0, 10);
    },
    hms() {
      return this.orderTime.slice(11);
    },
    isSale() {
      if(!this.$root.userInfo) return false;
      return this.dt.sellerId == this.$root.userInfo.userId && (this.dt.sellerType === "Member" || this.dt.sellerType === "Business");
    },
  },
};
</script>

