<template>
  <div class="hang-order" @click="$router.push(`/my_hang_order/${dt.pendingOrderId}?m=${dt.sellerType === 'Business'  ? 1 : 0}`)">
    <div class="time">
      挂单时间 {{ dt.createTime | dateFormat("M/D h:m:s") }}
    </div>
    <div class="content">
      <span class="payment-method">
        <div v-for="(item, index) in payments" :key="index" :class="`pay${item}`" class="item"/>
      </span>
      <span class="order-type">{{ dt.isSplit ? "可拆分" : "不可拆分" }}</span>
      <span class="amount">
        {{ dt.validAmount | amountFormat({places: 0}) }}
        V币
      </span>
      <span :class="stateClass" class="order-status">
        {{ stateStr }}
      </span>
      <van-icon name="arrow"/>
    </div>
  </div>
</template>

<script>
export default {
  name: "hangOrderItem",
  props: {
    dt: {
      type: Object,
      default: () => ({
        createTime: new Date().toLocaleDateString(),
        paymentType: 7, // 支付方式
        isSplit: 0, // 是否拆分
        amount: "13076598", // 金额
        status: 1, // 订单状态
        underwayOrder: 1,
      }),
    },
  },
  computed: {
    stateClass() {
      if (this.dt.underwayOrder > 0) return "underway";
      if (this.dt.status === 1) return "hanging";
      if (this.dt.status === 0) return "closed";
    },
    stateStr() {
      if (this.dt.frozen == 1) return "冻结";
      if (this.dt.underwayOrder > 0) return "已发起交易";
      if (this.dt.status === 1) return "挂单中...";
      if (this.dt.status === 0) return this.dt.amount == this.dt.soldAmount ? "已售罄" : "取消挂单";
    },
    payments() {
      return this.$root.parsePayments(this.dt.paymentType);
    },
  },
  methods: {},
};
</script>

