<template>
  <div class="page graybg page-my_hang_orders">
    <van-sticky offset-top="12.267vw">
      <van-dropdown-menu class="border_bottom_1px">
        <van-dropdown-item ref="payment_method" title="收款方式">
          <van-cell :border="false" class="selections">
            <van-checkbox-group v-model="query.paymentType">
              <van-checkbox :name="PAYMENT_METHOD.BANK" class="btn">
                <van-button slot="icon" class="btn"> 银行卡转账</van-button>
              </van-checkbox>
              <van-checkbox :name="PAYMENT_METHOD.WE_CHAT" class="btn">
                <van-button slot="icon" class="btn"> 微信转账</van-button>
              </van-checkbox>
              <van-checkbox :name="PAYMENT_METHOD.ALI" class="btn">
                <van-button slot="icon" class="btn"> 支付宝转账</van-button>
              </van-checkbox>
            </van-checkbox-group>
          </van-cell>
          <van-cell class="btns">
            <van-button class="cancel" type="danger" @click="resetPaymentMethod"> 重置 </van-button>
            <van-button class="confirm" type="primary" @click="load('payment_method')">
              确定
            </van-button>
          </van-cell>
        </van-dropdown-item>
        <van-dropdown-item ref="status" title="状态">
          <van-cell :border="false" class="selections">
            <div class="group">
              <van-checkbox v-model="query.isTransacting" class="btn">
                <van-button slot="icon" class="btn"> 已发起交易</van-button>
              </van-checkbox>
              <van-checkbox v-model="query.isHanging" class="btn">
                <van-button slot="icon" class="btn"> 挂单中</van-button>
              </van-checkbox>
            </div>
          </van-cell>
          <van-cell class="btns">
            <van-button class="cancel" type="danger" @click="resetStatus"> 重置 </van-button>
            <van-button class="confirm" type="primary" @click="load('status')"> 确定 </van-button>
          </van-cell>
        </van-dropdown-item>
        <van-dropdown-item ref="split_status" title="是否可拆分">
          <van-cell :border="false" class="selections">
            <van-radio-group v-model="query.isSplit" @change="(v) => onChangeSplitStatus(v)">
              <van-radio :name="1" class="btn">
                <van-button slot="icon" class="btn" @click="cancelSelect(1)"> 可拆分 </van-button>
              </van-radio>
              <van-radio :name="0" class="btn">
                <van-button slot="icon" class="btn" @click="cancelSelect(0)"> 不可拆分 </van-button>
              </van-radio>
            </van-radio-group>
          </van-cell>
        </van-dropdown-item>
      </van-dropdown-menu>
    </van-sticky>

    <van-list
      v-model="loading"
      :error-text="errorText"
      :error.sync="error"
      :finished="finished"
      finished-text="暂无更多数据"
      @load="search"
    >
      <HangOrderItem v-for="(item, index) in list" :key="index" :dt="item" />
    </van-list>
  </div>
</template>

<script>
import { HangOrderItem } from "../home/components";
import { myPendingOrderList } from "@/apis/market";
import { PAYMENT_METHOD } from "@/vendor/enum";
import tools from "@/vendor/tools";

export default {
  components: { HangOrderItem },
  data() {
    return {
      PAYMENT_METHOD,
      list: [],
      loading: false,
      error: false,
      finished: false,
      errorText: "加载失败，点击重试",
      page: 1,
      pageSize: 20,

      query: {
        isTransacting: false,
        isHanging: false,
        isSplit: "",
        paymentType: [],
      },
    };
  },
  watch: {
    query: {
      deep: true,
      handler() {
        this.page = 1;
        this.list = [];
      },
    },
  },
  created() {
    this.$root.$emit("root:set-topbar", ["left-back", "notices", "customer-service"]);
  },
  methods: {
    search() {
      myPendingOrderList(
        this.$tls.processParams(
          {
            page: this.page,
            pageSize: this.pageSize,
            isSplit: this.query.isSplit,
            paymentType: this.query.paymentType.reduce(
              (previous, current) => Number(previous) + Number(current),
              0
            ),
            inProgress: this.query.isTransacting ? 1 : "",
            status: this.query.isHanging ? 1 : "",
          },
          ["shake"]
        )
      ).then(({ ok, data, msg }) => {
        this.loading = false;
        if (ok) {
          if (this.page == 1) {
            this.list = data.list || [];
          } else {
            this.list.push(...(data.list || []));
          }
          this.finished = this.page * this.pageSize >= data.total;
          this.page += 1;
        } else {
          this.error = true;
          this.errorText = msg;
        }
      });
    },
    resetPaymentMethod() {
      this.query.paymentType = [];
      this.load("payment_method");
    },
    resetStatus() {
      this.query.isTransacting = false;
      this.query.isHanging = false;
      this.load("status");
    },
    load(key) {
      this.page = 1;
      this.$refs[key].toggle(false);
      this.search();
    },
    onChangeSplitStatus(v) {
      if (tools.isNumber(v)) {
        this.load("split_status");
      }
    },
    cancelSelect(v) {
      if (this.query.isSplit === v) {
        setTimeout(() => {
          this.query.isSplit = "";
          this.load("split_status");
        }, 50);
      }
    },
  },
};
</script>

